class AppHero extends HTMLElement {
  connectedCallback() {
    this.render();
  }

  render() {
    this.innerHTML = `
      <div class="hero">
        <div class="hero__inner">
          <h1 class="hero__title">Indonesian Restaurants</h1>
          <p class="hero__tagline">Find your type of restaurant here...</p>
        </div>
      </div>
    `;
  }
}

customElements.define('app-hero', AppHero);
